function roundUp(n, rnd) {
	if(!rnd) rnd = 1000;
	let dec;
	return (dec = n - (n |= 0))? ((dec * rnd + (dec < 0 ? -0.5 : 0.5)) | 0) / rnd + n : n;
}
String.prototype.capitalize = function() {
	return this.charAt(0).toUpperCase() + this.slice(1);
};

