function loadPoint(ball, frontPath) {
	let genDot = function() {
		let dots = ball.append('g').attrs({
			'class': 'dots'
		});
		let geoCircle = d3.geoCircle().radius(2.5).precision(22);
		let xNum = 50;
		let yNum = 15;
		let circles = new Array();
		// let rs = d3.scaleLinear()
		// 	.domain([0, 50])
		// 	.range([2, 1]);
		let xs = d3.scaleLinear()
			.domain([0, xNum])
			.range([-180, 180]);
		let ys = d3.scaleLinear()
			.domain([0, yNum])
			.range([0, 90]);
		for(let j = 0; j < xNum; j++) {
			for(let k = 0; k < yNum - 1; k++) {
				let xs = d3.scaleLinear()
					.domain([0, xNum - k * 3])
					.range([-180, 180]);
				let d = [xs(j), ys(k)];
				geoCircle.center(d);
				let circle = geoCircle();
				if(Math.random() > .5) {
					circle._hide = true;
				}
				circles.push(circle);
			}
		}
		for(let m = xNum; m > 0; m--) {
			for(let n = yNum - 2; n > 0; n--) {
				let xs = d3.scaleLinear()
					.domain([0, xNum - n * 3])
					.range([-180, 180]);
				let d = [xs(m), ys(n) * -1];
				geoCircle.center(d);
				let circle = geoCircle();
				if(Math.random() > .5) {
					circle._hide = true;
				}
				circles.push(circle);
			}
		}
		// console.log(circles.length);
		dots.selectAll('path')
			.data(circles)
			.enter()
			.append('path')
			.attr('class', function(d) { return d._hide? 'dot hide' : 'dot'; })
			.attr('d', frontPath);
	};
	genDot();
}
function loadHaiku(globe, svg) {
	import('../data/data.json').then(({default: data}) => {
		let dataKeyWords = new Array();
		let dataLines = {};
		for(let i = 0; i < data.length; i++) {
			let lines = data[i].content.split("\n");
			let syllable5 = new Array();
			let syllable7 = new Array();
			for(let j = 0; j < lines.length; j++) {
				lines[j] = lines[j].trim();
				let find5 = lines[j].indexOf('(5)');
				let find7 = lines[j].indexOf('(7)');
				if(find5 > -1) {
					syllable5.push(lines[j].slice(0, find5).trim().capitalize());
					continue;
				}
				if(find7 > -1) {
					syllable7.push(lines[j].slice(0, find7).trim().capitalize());
				}
			}
			dataLines[data[i].keyword] = {
				syllable5: syllable5,
				syllable7: syllable7
			};
			dataKeyWords.push(data[i].keyword);
		}
		// console.log(dataLines);
		let genLine = function(keyword, syllable) {
			if(!dataLines[keyword] 
				|| !dataLines[keyword]['syllable' + syllable]
				|| dataLines[keyword]['syllable' + syllable].length < 1) return false;
			let len = dataLines[keyword]['syllable' + syllable].length;
			let num = Math.floor(Math.random() * len);
			return dataLines[keyword]['syllable' + syllable][num];
		};
		let genHaiku = function() {
			if(selectedKeywords.length < 3) return;
			let rndKeywordId = selectedKeywords.sort(function(a, b) { return 0.5 - Math.random() });
			let rndKeywords = new Array();
			rndKeywordId.forEach(function(i) {
				rndKeywords.push(keyword.nodes()[i].querySelector('text').innerHTML);
			});
			let result = new Array(
				genLine(rndKeywords[0], 5),
				genLine(rndKeywords[1], 7),
				genLine(rndKeywords[2], 5)
			);
			for(let i = 0; i < result.length; i++) {
				if(result[i] === false) {
					genHaiku();
					return;
				}
			}
			d3.select('.result').html(result.join('<br />'));
			if(global.addthis_share) {
				global.addthis_share.title = result.join('\n') + '\n\n' + document.title;
				global.addthis_share.url = window.location.href;
			}
			// globe.append('div').attr('class', 'head result').html(result.join('<br />'));
		};
		let keywords = svg.append('g').attrs({
			class: 'keywords',
			transform: 'translate(' + fullSize / 2 + ', ' + fullSize / 2 + ')'
		});

		let r = d3.scaleLinear()
			.domain([0, dataKeyWords.length])
			.range([0, 360]);
		let y = d3.scaleLinear()
			.domain([0, 100])
			.range([ballSize / 4, 0]);
		let line = d3.line()
			.x(0)
			.y(function(d) { return y(d) - 122; });

		let tips = doc.querySelector('#globe .tips');
		let btnNext = doc.querySelector('#globe .btn.next');
		let btnCreate = doc.querySelector('#globe .btn.create');
		let btnSign = doc.querySelector('#globe .btn.sign');
		let btnCreateAnother = doc.querySelector('#globe .btn.create-another');
		let selectedKeywords = new Array();
		let filterKeywords = new Array();
		let updatedKeywords = function() {
			btnNext.classList.toggle('disabled', selectedKeywords.length < 3);
			btnCreate.classList.toggle('disabled', selectedKeywords.length < 3);
			gsap.set(tips, { autoAlpha: selectedKeywords.length > 0? 0 : 1 });
			keywords.classed('disable', selectedKeywords.length >= 3);
		};
		btnNext.addEventListener('click', function() {
			if(this.classList.contains('disabled'))
				return;
			gsap.to('#globeSvg .keywords', { autoAlpha: 0, duration: .4 });
			tips.classList.add('hide');
			doc.querySelector('#globe .selecteds').classList.add('hide');
			btnNext.classList.remove('show');
			doc.querySelector('#globe .prayers').classList.add('show');
			doc.querySelector('#globe .textarea').classList.add('show');
			btnCreate.classList.add('show');
		});
		btnCreate.addEventListener('click', function() {
			if(this.classList.contains('disabled'))
				return;
			doc.querySelector('#globe .selecteds').classList.add('hide');
			doc.querySelector('#globe .prayers').classList.remove('show');
			doc.querySelector('#globe .textarea').classList.remove('show');
			btnCreate.classList.remove('show');
			btnSign.classList.add('show');
			btnCreateAnother.classList.add('show');
			gsap.to('#globe .result', { autoAlpha: 1 });
			doc.querySelector('#globe .share').classList.add('show');
		});
		btnSign.addEventListener('click', function() {
			html.classList.add('dark-mode');
			gsap.to(main, { autoAlpha: 0, duration: .4 });
			gsap.to(doc.querySelector('section.petition'), { autoAlpha: 1, delay: .4 });
			gsap.set([
				partsEles.share,
				partsEles.btns.create,
				partsEles.btns.tohome,
				partsEles.btns.tosign
			], { autoAlpha: 0 });
			gsap.set([
				partsEles.by,
				partsEles.btns.goback
			], { autoAlpha: 1 });
		});
		btnCreateAnother.addEventListener('click', function(e) {
			btnSign.classList.remove('show');
			btnCreateAnother.classList.remove('show');
			gsap.to('#globe .result', { autoAlpha: 0 });
			doc.querySelector('#globe .share').classList.remove('show');
			selectedKeywords = new Array();
			// gsap.set([
			// 	partsEles.temp,
			// 	partsEles.btns.create
			// ], { autoAlpha: 0 });
			// gsap.set([
			// 	partsEles.btns.tohome
			// ], { autoAlpha: 1 });
			d3.select('.selecteds').html('');
			d3.selectAll('#globeSvg .keyword.on').classed('on', false);
			updatedKeywords();
			doc.querySelector('#globe .selecteds').classList.remove('hide');
			btnCreate.classList.add('show');
			gsap.to('#globeSvg .keywords', { autoAlpha: 1 });
			// startHaiku();
		});
		let addSelectedKeywords = function(word, j) {
			let selected = d3.select('.selecteds')
				.append('div')
				.attr('class', 'selected')
				.attr('key', j);
			selected.append('div')
				.attr('class', 'close')
				.on('click', function(d, i) {
					let key = selectedKeywords.indexOf(j);
					selectedKeywords.splice(key, 1);
					selected.remove();
					keyword.nodes()[j].classList.remove('on');
					updatedKeywords();
				});
			selected.append('div')
				.attr('class', 'head text')
				.text(word);
				// .html('<div class="close"></div><div class="head text">' + word + '</div>');
			updatedKeywords();
		};
		let keyword = keywords.selectAll('.keyword')
			.data(dataKeyWords)
			.enter()
			.append('g')
			.attrs({
				transform: function(d, i) { return 'rotate(' + (r(i) + 90) + ') translate(' + y(-120) + ', 0)'; },
				class: function(d, i) { return 'keyword'; },
			}).on('click', function(d, i) {
				if(started) {
					let key = selectedKeywords.indexOf(i);
					if(key > -1) {
						this.classList.remove('on');
						selectedKeywords.splice(key, 1);
						doc.querySelector('#globe .selected[key="' + i + '"]').remove();
						updatedKeywords();
						return;
					}
					if(selectedKeywords.length >= 3) return;
					this.classList.add('on');
					selectedKeywords.push(i);
					keywords.classed('disable', selectedKeywords.length >= 3);
					addSelectedKeywords(d, i);
					if(selectedKeywords.length >= 3)
						genHaiku();
				} else {
					let key = filterKeywords.indexOf(i);
					if(key > -1) {
						this.classList.remove('on');
						filterKeywords.splice(key, 1);
						// updatedKeywords();
						return;
					}
					this.classList.add('on');
					filterKeywords.push(i);
					// addSelectedKeywords(d, i);
				}
			});
		keyword.append('path').attrs({
			d: function(d) { return line([0, 8]); },
			transform: function(d, i) { return 'rotate(90)'; },
			stroke: '#525252',
			'stroke-width': .25,
			'stroke-linecap': 'round'
		});
		keyword.append('rect').attrs({
			y: -28,
			x: -20,
			width: 100,
			height: 60,
			fill: 'transparent'
		});
		keyword.append('g').attrs({
			class: 'text'
		}).append('text').attrs({
			dy: '0.31em',
			'text-anchor': function(d, i) {
				let deg = r(i);
				return deg >= 0 && deg <= 180? 'end' : 'start';
			},
			transform: function(d, i) {
				let deg = r(i);
				return deg >= 0 && deg <= 180? 'rotate(180)' : null;
			}
		})
		// .attr('class', 'head')
		.text(function(d) { return d; });
		// genDot();
	});
}
