function startHaiku() {
	started = true;
	let globe = doc.getElementById('globe');
	let steps = globe.querySelectorAll('.step');
	let selectWords = globe.querySelectorAll('.select-word');
	let nextStep = function(i) {
		gsap.set(steps, { autoAlpha: 0 });
		if(i < steps.length) {
			gsap.set(steps[i], { autoAlpha: 1 });
			steps[i].querySelector('.txtbox').focus();
			// console.log(steps[i].querySelector('.txtbox'));
		} else {
			var tl = gsap.timeline({});
			tl.to(selectWords[0], { autoAlpha: 1 });
			tl.to(selectWords[0], { autoAlpha: 0 });
			tl.to(selectWords[1], { autoAlpha: 1 });
			tl.to('#globeSvg .keywords', { autoAlpha: 1 });
		}
	};
	// global.addthis_share.description = 'THE DESCRIPTION\nLine2';
	globe.querySelectorAll('.answer').forEach(function(e, i) {
		let that = e;
		let txtbox = e.querySelector('.txtbox');
		let enter = e.querySelector('.icon-enter');

		autosizeInput(txtbox, { minWidth: true });
		txtbox.addEventListener('input', function() {
			that.classList.toggle('entered', this.value != '');
		});
		enter.addEventListener('click', function() {
			nextStep(i + 1);
		});
		txtbox.addEventListener('keypress', function(e) {
			if(e.keyCode == 13) {
				nextStep(i + 1);
			}
		});
	});
	gsap.to('#globeSvg .keywords', { autoAlpha: 0, duration: .4 });
	gsap.to('#globeSvg .ball', { autoAlpha: .5, duration: .4 });
	gsap.to('#globeSvg .dots', { autoAlpha: .2, duration: .4 });
	gsap.set('#globeSvg .dots', { 'pointer-events': 'none' });
	gsap.to(steps[0], { autoAlpha: 1, duration: .4, onComplete: function() {
		steps[0].querySelector('.txtbox').focus();
	} });
}
